@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=text],
input[type=email],
input[type=password],
input,
textarea {
  outline: none;
  padding: 0;
  margin: 0;
  border: none;
}

a {
  text-decoration: none; /* Прибрати підкреслення */
  color: inherit; /* Встановити колір тексту як у батьківського елемента */
  font-weight: normal; /* Нормальна вага шрифту */
  font-style: normal; /* Нормальний стиль шрифту */
  background: none; /* Прибрати фон */
  border: none; /* Прибрати бордюри */
  padding: 0; /* Прибрати відступи */
  margin: 0; /* Прибрати поля */
  outline: none;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}

#content, .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 15px;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  background-color: #333;
  padding: 20px 0;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .logo {
  color: #fff;
  font-size: 24px;
  font-weight: bold;
}
header nav ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
header nav ul li {
  margin-left: 20px;
}
header nav ul li a {
  color: #fff;
  font-size: 16px;
  transition: background 0.3s;
}
header nav ul li a:hover {
  background-color: #1a1a1a;
  border-radius: 5px;
}

.hero {
  background: url("https://via.placeholder.com/1200x400") no-repeat center center/cover;
  color: #fff;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.hero h1 {
  font-size: 48px;
  margin-bottom: 20px;
}
.hero p {
  font-size: 20px;
  margin-bottom: 30px;
}
.hero .cta-button {
  background-color: #e63946;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background 0.3s;
}
.hero .cta-button:hover {
  background-color: #d21a28;
}

.about-section {
  background-color: #fff;
  padding: 40px 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
  text-align: center;
}
.about-section h2 {
  font-weight: 600;
  font-size: 3rem;
  text-align: center;
  padding-bottom: 30px;
}
.about-section .about-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
}
.about-section .about-content {
  text-align: left;
}
.about-section .about-content p {
  font-size: 1.2rem;
  color: #444;
}

.team-section {
  background-color: #f4f4f4;
  padding: 40px 20px;
}
.team-section h2 {
  text-align: center;
  font-weight: 600;
  padding-bottom: 20px;
}
.team-section .team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.team-section .team-members .team-member {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 300px;
  text-align: center;
  margin-bottom: 20px;
  padding: 20px;
  transition: transform 0.3s ease;
}
.team-section .team-members .team-member:hover {
  transform: translateY(-10px);
}
.team-section .team-members .team-member img {
  max-width: 100%;
  border-radius: 50%;
  margin-bottom: 15px;
}
.team-section .team-members .team-member h3 {
  color: #333;
  margin-bottom: 10px;
}
.team-section .team-members .team-member p {
  color: #444;
  font-size: 1rem;
}

.about-us .container {
  padding: 40px 15px !important;
}
.about-us .container h2 {
  font-size: 48px;
  margin-bottom: 20px;
  text-align: center;
}
.about-us .container p {
  font-size: 20px;
  margin-bottom: 30px;
}

.about-business {
  padding: 40px 0;
  background-color: #fff;
  text-align: left;
}
.about-business .about-content {
  margin-bottom: 30px;
}
.about-business .about-content h2 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 10px;
}
.about-business .about-content p {
  font-size: 1rem;
  line-height: 1.6;
  color: #444;
}
.about-business .about-image {
  text-align: center;
  margin-top: 40px;
}
.about-business .about-image img {
  max-width: 100%;
  height: auto;
  border: 1px solid #333;
}

.products {
  padding: 50px 0;
}
.products h2 {
  text-align: center;
  margin-bottom: 40px;
}
.products .product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}
.products .product-grid .product-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}
.products .product-grid .product-card img {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 15px;
}
.products .product-grid .product-card h3 {
  margin-bottom: 10px;
  font-size: 20px;
}
.products .product-grid .product-card p {
  color: #444;
  padding-bottom: 30px;
}

.contact-section {
  background-color: #fff;
  padding: 40px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}
.contact-section h2 {
  color: #e63946;
  font-size: 2rem;
  margin-bottom: 20px;
}
.contact-section .contact-details {
  margin-bottom: 40px;
}
.contact-section .contact-details h2 {
  text-align: center;
}
.contact-section .contact-details p {
  font-size: 1.1rem;
}
.contact-section .contact-details .detail-item {
  margin-bottom: 10px;
}
.contact-section .contact-details .detail-item strong {
  color: #333;
  font-weight: bold;
}
.contact-section form {
  background-color: #f4f4f4;
  padding: 20px 0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.contact-section form label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: #333;
}
.contact-section form input, .contact-section form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.contact-section form button {
  padding: 10px 20px;
  background-color: #e63946;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 4px;
}
.contact-section form button:hover {
  background-color: #d21a28;
}

.popup {
  display: none; /* Приховуємо popup за замовчуванням */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Напівпрозорий фон */
  justify-content: center;
  align-items: center;
}
.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 90%;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: relative;
}
.popup-content h2 {
  color: #e63946;
  margin-bottom: 15px;
}
.popup-content p {
  font-size: 16px;
  margin-bottom: 20px;
}
.popup-content .close-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 24px;
  color: #333;
  cursor: pointer;
}

.policy-section {
  background-color: #fff;
  padding: 40px 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}
.policy-section .heading {
  font-weight: 600;
  font-size: 3rem;
  text-align: center;
  padding-bottom: 30px;
}
.policy-section h2 {
  font-size: 2rem;
  margin-bottom: 15px;
}
.policy-section p {
  font-size: 1.1rem;
  margin-bottom: 20px;
  color: #444;
}
.policy-section ol {
  margin-left: 20px;
  list-style: decimal;
}
.policy-section ol li {
  font-size: 1rem;
  margin-bottom: 15px;
  color: #444;
}
.policy-section ul {
  margin-left: 20px;
  list-style: disc;
}
.policy-section ul li {
  font-size: 1rem;
  margin-bottom: 15px;
  color: #444;
}

.terms-section {
  background-color: #fff;
  padding: 40px 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}
.terms-section .heading {
  font-weight: 600;
  font-size: 3rem;
  text-align: center;
  padding-bottom: 30px;
}
.terms-section h2 {
  font-size: 2rem;
  margin-bottom: 15px;
}
.terms-section p {
  font-size: 1.1rem;
  margin-bottom: 20px;
  color: #444;
}
.terms-section .terms-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
  border-radius: 8px;
}

footer {
  background-color: #333;
  color: #fff;
  padding: 50px 0;
  text-align: center;
}
footer h2 {
  margin-bottom: 20px;
}
footer .links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
footer form {
  margin-top: 20px;
}
footer form input[type=email] {
  padding: 10px;
  width: 250px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}
footer form button {
  padding: 10px 20px;
  background-color: #e63946;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}
footer form button:hover {
  background-color: #d21a28;
}

.cta-button {
  background-color: #e63946;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background 0.3s;
}
.cta-button:hover {
  background-color: #d21a28;
}